//======================= BASE =================//
hr {
  background: $background-black;
}

::-moz-selection {
  color: $text-white;
  background: $background-black;
}

::selection {
  color: $text-white;
  background: $background-black;
}

h1, h2, h3, h4, h5, h6, .h1-style, .h2-style, .h3-style, .h4-style, .h5-style, .h6-style{
  color: $heading-color;
}

.paris1-bg-blue {
  background-color: $background-main-color;
  &.paragraph--type--bp-accordion{
    .titre {
      color: $text-white;
    }
    ul.accordion {
      background: none;
      .paragraph--type--bp-simple {
        p {
          color: $text-black;
        }
      }
    }
  }
}
.paris1-bg-blue:not(.paragraph--type--bp-accordion) {
  *{color: $text-white;}
  .paragraph--type--bp-simple {
    p {
      color: $text-white;
    }
  }
}
.paris1-bg-soft-grey{
  background-color: $background-light-gray;
  &.paragraph--type--bp-accordion{
    ul.accordion {
      background: none;
    }
  }
}

.mask{
  @include maskBackground($background-main-color, 0.6);
}
.mask-effect{@include maskBackground($background-black);}
.mask-effect2{@include maskBackground($background-black, 0.1, 0.4);}
.mask-effect-3{@include maskBackground($background-black, 0.3);}
.mask-effect-4{@include maskBackground($background-black, 0.4);}
.mask-effect-5{@include maskBackground($background-black, 0.5);}
.mask-effect-7{@include maskBackground($background-black, 0.7);}

.m-txt-message, .message{
  *{color: inherit;}
  background-color: $light-blue;
  &.valid{
    color: $green;
    background-color: $light-green;
  }
  &.error{
    color: $pink;
    background-color: $light-pink;
  }
  &.warning{
    color: $yellow;
    background-color: $light-yellow;
  }
}
ul.list-style {
  li{
    &:before{
      background-color: $background-main-color;
    }
  }
}

.homepage-search-form{
  #edit-submit, #edit-submit--2{
    &:hover{
      color: $text-main-color;
    }
  }
}
tbody{
  background-color: transparent;
  tr, td{background-color: transparent; padding: 0;}
}
.circle{
  @include circleStyle();
}
.facets-widget-checkbox{
  .js-facets-checkbox-links{
    .facet-item{
      label{
        border: 1px solid $text-main-color;
        background-color: transparent;
      }
      input[checked="checked"] + label{
        background-color: $background-main-color;
        color: $text-white;
        &:after{
          color: $background-main-color;
          background-color: $text-white;
          border: 1px solid $background-main-color;
        }
      }
    }
  }
}
#search-toggle{
