}
.paragraph--type--files-media{
  span.fa{
    &:before{color: $text-main-color;}
  }
}
.categ-style{color: $text-white; background-color: $background-main-color;}
.more-link, .load-more{color: $text-main-color;}
.date-style{color: $text-black;}
.author{
  color: $text-main-color;
}
.field--name-field-credits{
  background-color: rgba($background-black, 0.7);
  color: $text-white;
}
.paragraph--type--bp-view{
  .field--name-field-publication-topics{
    color: $text-main-color;
  }
}
.paragraph--type--section-presentation{
  .paragraph--type--presentation{
    color: $dark-gray2;
    h3{
      color: $gold-p1ps;
    }
  }
}
.paragraph--type--bp-simple{
  p{
    color: $dark-gray2;
  }
  .paris1-bg-blue &{
    p{color: $text-white;}
  }
}
.view-header-color{
  background-color: $background-light-gray;
}
.key-figures-section {
  .chiffre{
    color: $gold-p1ps;
  }
  .legende{
    color: $text-main-color;
  }
}

button.semi-oval{
  background-color: $background-light-gray; color: $text-main-color;
  border: 1px solid $text-white;
  @include transitionEffect();
  &.is-active, &:hover{
    background-color: $background-main-color; color: $text-white;
  }
}

.swiper-container{
  .swiper-button-prev, .swiper-button-next{
    &:before{
      color: $background-main-color;
    }
  }
  &:not(.swiper-tiles):not(#swiper-carousel){
    .swiper-button-prev, .swiper-button-next{
      background-color: $white;
      &:before{
        color: $text-black;
      }
      &.swiper-button-disabled{
        background-color: transparent;
      }
    }
  }
  &#swiper-carousel{
    .swiper-pagination{
      .swiper-pagination-bullet.swiper-pagination-bullet-active{
        background-color: $background-main-color;
      }
    }
    .play-pause-button{
      span:before{
        color: $background-main-color;
      }
    }
  }
}

.accordion-wrapper{
  .accordion {
    > li{
      border: 2px solid $text-main-color; @include box-shadow(0.25, 0, 0, 4px, $text-black);
      .accordion-title{
        color: $text-main-color; background-color: $background-white;
        &:hover{color: $text-main-color; background-color: $background-white;}
      }
      &.is-active{
        .accordion-title{color: $text-main-color; background-color: $background-white;}
      }
    }
  }
}

.overlay-infos, .main-overlay-infos{
  color: $text-white;
  * {color: $text-white;}
  a:hover{color: $text-white;}
}

.mini-site-back-wrapper{
  h5{color: $text-main-color;}
  .mini-site-circle{
    border: 1px solid $background-main-color; background-color: $background-main-color;
    color: $text-white;
  }
  &:hover{
    .mini-site-circle{background-color: $text-white; color: $background-main-color;}
  }
  .transparent:not(.scroll) &{
    .mini-site-circle{
      background-color: transparent; border: 1px solid $background-white;
    }
    &:hover{
      .mini-site-circle{
        background-color: $background-white; color: $text-main-color;
      }
    }
    h5{color: $text-white;}
  }
}
//====================FIN BASE ==================//


// ================ FORM =======================//
.site-content-wrapper input[type="submit"]:not(.reset-btn){
  border: 2px solid $background-main-color;
}
.centres-up1-form, .center-wrapper {
  .map-centre {
    .overlay-infos{
      .form-item{
        label{
          color: $text-white !important;
        }
      }
    }
  }
}
textarea,
.chosen-container-single .chosen-single,
input[type="text"],
input[type="email"],
input[type="tel"],
input[type="submit"]:not(.reset-btn),
input[type="number"],
input[type="button"],
input[type="password"],
input[type="url"],
input[type="file"],
input[type="search"], .form-date, select.form-select
.file-widget {
  @include inputStyle();
}
.form-item:not(.form-item-centre-name), .chosen-container-single{
  &:before{
    background-color: $text-main-color;
  }
}
.chosen-container-single{
  .chosen-single{
    &:after{
      color: $text-main-color;
    }
    span{
      color: $dark-gray3;
    }
  }
  .chosen-drop{
    .chosen-search{
      color: $text-main-color;
    }
    ul.chosen-results{
      li{
        background-color: $background-white;
        color: $text-main-color;
        &.highlighted, &.result-selected{
          background-color: $background-main-color;
          color: $text-white;
        }
      }
    }
  }
  .chosen-container-active.chosen-with-drop{
    border: 2px solid $background-main-color !important;
  }
}
.user-is-tabbing{
  textarea, select,
  input[type="text"],
  input[type="email"],
  input[type="tel"],
  input[type="number"],
  input[type="button"],
  input[type="password"],
  input[type="url"],
  input[type="file"],
  input[type="search"],
  [type="submit"],
  [type="date"],
  [type="datetime"],
  [type="datetime-local"],
  [type="month"],
  [type="week"],
  [type="time"],
  [type="color"],
  .file-widget{
    &:focus{
      //border: 2px solid $gray;
      outline: 0;
    }
  }
}


//==== CHECKBOXES et RADIO====
.form-type-radio, .form-type-checkbox,
[class*="form-type-checkbox"], [class*="form-type-radio"]{
  input{
    &:checked + label{
      span{
        &:before{background-color:$background-main-color;}
        &:after{background-color:$text-white;}
      }
    }

    &:focus + label{
      .user-is-tabbing &{
        border: solid 2px $text-black;
        outline: 0;
      }
    }
  }//close input

  label, .label-style{
    &.disabled{
      background-color: transparent !important;
      color: $medium-gray;
    }
    &:hover{
      color: $text-black;
      span{
        &:before{
          background-color: $light-blue;
        }
        &:after{
          background-color: $background-main-color;
        }
      }
    }
    span{
      &:before {
        background-color: $medium-gray;
      }
      &:after{
        //background-color: $background-black;
      }
    }
  }//close label / label-style
}//close .form-type-radio, .form-type-checkbox

[class*="form-type-checkbox"]{
  input{
    &:checked + label, &:hover + label{
      span{
        &:after{background-color:transparent;}
      }
    }
  }
}

//CSS label
label, .label-style{
  color: $text-black;
}

// ================== SELECT STYLE =================== //
.form-type-select, .select-wrapper{
  &.focus, &:focus{
    &:before{color:$text-black;}
    select{
      color:$text-black;
    }
  }
  &:hover{
    color:$text-black;
    select{color:$text-black;}
  }
  select{
    background-color: $background-white;
    &.horaires-select{
      background-color:$gray;
      border-color:$gray;
      &:hover{
        //background-color: $medium-gray;
        //background-color: $background-white;
      }
    }
  }
}
// ======== SELECT STYLE END ======== //
// ================= FIN FORM ==================//

//================== RESET ==================//
mark {
  background: #ff0;
  color: #000;
}

fieldset {
  border: 1px solid #c0c0c0;
}


.password-strength__meter{background-color: $medium-gray;}
.password-strength__indicator{background-color: $background-main-color;}
