//=============== TABLE =================== //
.responsive-card-table {
  thead tr{
    background-color: $medium-gray;
    th {
      font-weight: normal; line-height: 1.43;
    }
  }

  tbody tr{
    background-color: $light-blue;
    td {
      @extend .txt-black;
    }
  }
  tr{
    border: 1px solid $text-white;
  }
