//================ FOOTER =================== //
.footer{
  .footer-wrapper{
    .cell{
      &:not(:last-child){
        &:after{
          background-color: $default-border-color;
        }
      }
    }
    .two-columns{
      a{color: $text-main-color;}
      &:after{
        background-color: $default-border-color;
      }
    }
    .block-title{
      color: $text-main-color;
    }
  }

}//close .foooter

#region-footer{
  .footer-center-inner{
    li{
      a{
        &:hover{
          color:$text-white;
          &:before{
            background-color: $background-white;
          }
        }
      }
    }
  }
  .footer-right-inner{
    ul{
      li{
        a{
          &:hover{color:$text-white;}
        }
      }
    }
  }
}
//================ FIN FOOTER =============== //

//================ HEADER =================== //
#navbar{
  #top-header {
    background-color: $background-white;
    @include box-shadow(0.25, 3px, 0, $background-black);
    .site--type--mini-site.page-mini-site &, .site--type--mini-site.path-user &, .site--type--mini-site.transparent.scroll &{
      .top-header-infos{
        @include box-shadow(0.25, 3px, 0, $background-black);
      }
    }
    .header-micro{
      .micro-left{
        .site-name{
          &:before{
            background-color: $light-gray1;
          }
          .transparent:not(.scroll) &{
            &:before{background-color: $text-white;}
            h2{color: $text-white;}
          }
        }
      }
      .micro-right{
        a{color: $dark-gray2;}
        .transparent:not(.scroll) &{
          a{color: $text-white;}
        }
      }
    }
  }//close top header

  .icon-menu{
    span.toggle-container{
      >span{
        background-color: $text-main-color;
      }
    }
  }//close icon-menu


  //.header-right
  .header-right{
    .language-switcher-language-url {
      ul {
        background-color: $background-white; @include box-shadow();
        &:before{
          border-bottom: solid rem-calc(6px) $background-white;
        }
      }
    }
    .block-up1-ent-search-block{
      nav {
        ul {
          #ent{
            a{
              background-color: $background-main-color;
              color: $text-white;
              .fa{color: $text-white;}
              &:hover{
                @include box-border(1px, $default-border-color);
                background-color: $default-border-color;
                color: $text-main-color;
                .fa{color: $text-main-color;}
              }
            }
          }
        }
      }
    }
  }//close header-right
  .block-views-exposed-filter-blockrecherche-de-contenu-page-1,
  .block-views-exposed-filter-blockrecherche-db-page-1{
    background-color: $background-white;
    @include box-shadow(0.25, 2px, 0, $background-black);
    #views-exposed-form-recherche-de-contenu-page-1{
      // .form-item{
      // 	input{
      // 		border-bottom: 2px solid $text-light-gray;
      // 	}
      // }
    }
  }
  .accessibility-wrapper{
    #accessibility-toggle{
      @include circleStyle();
      &:before{
        background-color: $text-main-color;
      }
      &:after{
        background-color: $text-white;
      }
    }
    &:hover, &.overlay-show{
      #accessibility-toggle{
        border: 1px solid $default-border-color;
        &:after{
          transform: translateX(1px) translateY(-5px) rotate(135deg) !important;
        }
      }
    }
    .accessibility-mode &{
      &:hover, &.overlay-show{
        #accessibility-toggle{border: none;}
      }
    }
    #accessibility-container{
      background-color: $background-white;
      @include box-shadow();
      span{color: $text-black;}
      &:before{
        border-bottom: solid rem-calc(6px) $background-white;
      }
    }
    &.overlay-show, &:hover{
      #accessibility-toggle{
        background-color: $default-border-color;
        //&:before{background-color: $text-white;}
      }
    }
  }
}//close navbar

//open navbar in transparent mode
.transparent:not(.scroll) #navbar{
  #top-header{
    background-color: transparent;
    box-shadow: none;
    svg{
      fill: $text-white;
      *{fill: inherit;}
    }
  }

  .icon-menu{
    span.toggle-container{
      >span{
        background-color: $text-white;
      }
    }
  }
  .header-right{
    .block-up1-ent-search-block{
      nav {
        ul {
          #ent{
            a{
              @include box-border(1px, $text-white);
              color: $text-white;
              .fa{
                color: $text-white;
              }
              &:hover{
                background-color: $text-white;
                color: $background-main-color;
                .fa{color: $background-main-color;}
              }
            }
          }
        }
      }
    }
  }//close header-right
  .accessibility-wrapper{
    #accessibility-toggle{
      &:before{
        background-color: $text-white;
      }
      &:hover{
        &:before{
          background-color: $background-main-color;
        }
      }
    }
    &.overlay-show{
      #accessibility-toggle{
        @include box-border(1px, $text-main-color);
        background-color: $text-white; color: $background-main-color;
        *{color: $text-main-color;}
        &:before{
          background-color: $background-main-color;
        }
      }
    }
  }
  .circle, #accessibility-toggle{
    &:not(.white){
      @include box-border(1px, $text-white);
      color: $text-white;
      *{color: $text-white;}
      &.overlay-show, &:hover{
        @include box-border(1px, $text-main-color);
        background-color: $text-white; color: $background-main-color;
        *{color: $background-main-color;}
      }
    }
  }
}// close transparent body

#region-header {
  background-color: $background-white;
  .header-right{
    .header-right-inner{
      #block-headerrightmenu{
        >ul {
          >li{
            span, a{color: $text-main-color;}
            ul{
              background-color: $background-white;
              li{
                a{
                  &:hover{background-color: $background-light-gray;}
                }
                &:not(:last-of-type){
                  border-bottom: 1px solid $medium-gray;
                }
              }
            }
          }
        }
      }
    }
  }
  #block-mainnavigation{
    > .menu{
      > li {
        span , a{
          color: $text-main-color;
        }
        .li-menu-wrapper{
          > span, > a{
            &:hover:not([class*="icon-"]){
              color: $text-main-color;
            }
          }
        }
        > span, > a{
          &:hover:not([class*="icon-"]){
            color: $text-main-color;
          }
        }
        &.has-children{
          ul{
            background-color: $background-white;
            li{
