              }
            }
          }
        }
      }
    }
  }
  border-bottom:2px solid $medium-gray;
}//close region header
#navbar-header{
  background-color: $background-main-color;
  .navigation-left-region{
    background-color: $background-white;
    ul{
      li{
        a{
          color: $text-black;
        }
      }
    }
  }
  .navigation-region{
    ul {
      li {
        a,span{
          color: $text-white;
        }
      }
    }
  }
}
