// =============== MAIN ================= //
.pager a.button{
  background-color: $background-white;
  border: 1px solid $text-main-color;
  color: $text-main-color;
  &:hover{
    background-color: $background-main-color;
    color: $text-white;
  }
}
.pagination{
  .pager__item{
    background-color: transparent;
    a, a:hover{
      background-color: transparent;
      color: $text-black;
    }
    &.is-active{
      a{background-color: transparent;}
    }
    &.current{
      a{color: $gray;}
    }
    &:hover{
      a{
        &:after{
          background-color: $light-blue;
        }
      }
    }
    &.pager__item--previous, &.pager__item--next{
      &:hover{
        span:after{color: $text-white;}
        a:after{
          background-color: $background-main-color;
        }
      }
    }
    &.pager__item--first,&.pager__item--last{
      &:hover{
        span:after{color: $text-white;}
        a{
          color:$text-white;
          &:after{
            background-color: $background-main-color;
          }
        }
      }
    }
  }
}

.breadcrumbs{
  a{
    &:before{
      background-color: #212529;
    }
  }
}
.event {
  .field-add-to-cal{
    color: $dark-gray2;
    a{
      color: $dark-gray2;
    }
  }
  .social-and-calendar {
    .field-add-to-cal{
      #{$border-right}: 1px solid $dark-gray3;
    }
  }
  .social-buttons-links{
    li{
      a{
        color: $text-main-color;
      }
    }
  }
}

// ================ FIN MAIN ============ //

// ============= BTN ================ //
.site-content-wrapper{
  .paragraph {
    .cell.outline {
      .btn {
        border-color: $text-white; color: $text-white; background-color: transparent;
        &:hover{
          background-color: $background-white; color: $text-main-color;
          *{color: $text-main-color;}
        }
      }
    }
  }
  .btn, .button--primary,
  button:not(.reset-btn):not(.trigger):not(.link-style):not(.icon-container):not(.toolbar-item):not(.toolbar-icon),
  input[type="submit"].button {
    color: $text-main-color; background-color: $background-white; border-color: $text-main-color;
    &:hover{color: $text-white; background-color: $background-main-color;}

    &.outline{
      border-color: $text-white; color: $text-white; background-color: transparent;
      &:hover{
        background-color: $background-white; color: $text-main-color;
        *{color: $text-main-color;}
      }
    }
  }
  //close btn
}//close .site-content-wrapper

//_box-style.scss ====================
.box-style{
  //box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.25);
  //&.no-shadow{box-shadow: none;}
  .box-content{
    &:not(.no-bg):not(.blue-box) {
      background-color: $background-light-gray;
      &.white {
        background-color: $white;
      }
    }
    h2{
